import { useEffect, useRef, useMemo, useState } from 'react'
import { models } from './domain'
import { useFBX, Clone } from '@react-three/drei'
import * as THREE from 'three'
import { useSnapshot } from 'valtio'
import { state } from './store'



function Building({filename}) {
  const building = useFBX(filename+'.fbx')
  console.log({building})
  const [selected, setSelected] =  useState(false);
  const building_selected = building.clone()
  const BasicMaterial = new THREE.MeshBasicMaterial({color: new THREE.Color("#520B3E")})
  building_selected.children.forEach((mesh,i) => mesh.material = BasicMaterial)
  building.castShadow=false
  return <primitive scale={0.1}

                    object={selected ? building_selected : building}
                    onClick={(x,y) => {
                      console.log(filename)
                    }}

         />
}

export default function Buildings() {
  var snap = useSnapshot(state)
  const visible = snap.showFuture ? "future" : "existing"
  const buildings = models.filter(it => it.states.includes(visible))
  return <>
           {buildings.map(it => <Building key={it.name} filename={it.name}/>)}
         </>
}

