
import { Logo } from '@pmndrs/branding'
import { motion, AnimatePresence } from 'framer-motion'
import { AiFillCamera, AiOutlineArrowLeft, AiOutlineHighlight, } from 'react-icons/ai'
import { useSnapshot } from 'valtio'
import { state } from './store'

export default function Overlay() {
  var snap = useSnapshot(state)

  return (
    <>
      <div style={{ position: 'absolute', bottom: 120, left: 120, fontSize: '18px' }}>
        <div style={{ position: 'relative', marginTop: 10, display: 'inline-block' }}>
          <button style={{ background: snap.color }}
            onClick={() => (state.showFuture = !state.showFuture)}>
            {snap.showFuture ? 'Future' : 'Present'}
          </button>
        </div>
      </div>
    </>
  )
}
